import { writable } from 'svelte/store';
function appData() {



	const { subscribe, update } = writable({
        is_online: false,

        /**
         * lleva a la ruta stream.3pod.io/
        */
        host: "",

        /**
         * lleva a la ruta stream.3pod.io/api/v1/services
        */
        api_host: "",

        /**
         * lleva a la ruta stream.3pod.io/
        */
         api_host_base: "",

        /**
         * lleva a la ruta default/public/assets/
         */
        assets_path: "",
        website_title: "",
        plan_limits_testing: "",
        go_to_plans: "#",
        projects: [],
        path: "/",
        user: {},
        host: null,
	});
	
	const dataApp = {
        subscribe: subscribe,
        addMethod: function(field, func){
            if(typeof func !== "function"){
                throw("Error, '"+field+"' is not a function");
            }
            update(( data )=>{
                data.methods[field] = func;
                return data;
            })
        },
        update: function(field, new_data){            
            update(( data )=>{
                data[field] = new_data;
                return data;
            })
        },
        get: function(){
            let returns = {};
            update(function( data ){
                returns = data;
                return data;
            })
            return returns;
        }
    }

    return dataApp;
}


const ApplicationData = appData();

export {ApplicationData}
